"use client";

import Image from "next/image";
import Link from "next/link";
import { teamMembers } from "../../data/aboutData";

interface PageProps {
  params: { id: string };
}

export default function MemberDetail({ params }: PageProps) {
  const { id } = params;
  const member = teamMembers.find((m) => m.id === id);

  if (!member) {
    return (
      <main className="min-h-screen flex items-center justify-center bg-gradient-to-b from-gray-100 to-gray-200">
        <p className="text-gray-500 text-lg font-medium">Profile not found.</p>
      </main>
    );
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-white via-sky-50/30 to-white py-24 px-6">
      {/* Profile Section */}
      <div className="max-w-6xl mx-auto grid md:grid-cols-2 gap-16 items-center">
        {/* Left: Floating Portrait */}
        <div className="relative flex justify-center">
          <div className="relative w-72 h-72 md:w-96 md:h-96 rounded-3xl overflow-hidden shadow-[0_16px_60px_-16px_rgba(56,189,248,0.35)] ring-4 ring-white/50 transition-transform duration-700 ease-out hover:-translate-y-2 hover:scale-105">
            <Image
              src={member.image}
              alt={member.name}
              fill
              className="object-cover scale-105 transition-transform duration-700 ease-out"
            />
          </div>
          {/* Soft Halo Glow */}
          <div className="absolute inset-0 blur-3xl bg-sky-100/30 rounded-3xl -z-10 scale-125 opacity-90 animate-pulse-slow"></div>
        </div>

        {/* Right: Text Content */}
        <div className="space-y-8">
          <h1 className="text-5xl md:text-6xl font-extrabold tracking-tight bg-gradient-to-r from-sky-600 to-blue-800 bg-clip-text text-transparent drop-shadow-md">
            {member.name}
          </h1>
          <h2 className="text-xl font-semibold text-sky-700 uppercase tracking-wide">
            {member.title}
          </h2>

          <p className="text-gray-700 text-lg leading-relaxed max-w-prose text-justify drop-shadow-sm">
            {member.bioLong || member.bio}
          </p>

          {member.quote && (
            <blockquote className="text-sky-700 italic border-l-4 border-sky-200 pl-4 mt-6 drop-shadow-sm">
              ‚Äú{member.quote}‚Äù
            </blockquote>
          )}

          <div className="pt-8 text-sm text-gray-500 border-t border-gray-200 leading-relaxed space-y-1">
            <p>
              ‚úçÔ∏è Written by <span className="font-medium">{member.author}</span>
            </p>
            <p>üìÖ Joined: {member.createdAt}</p>
            {member.updatedAt && (
              <p className="text-sky-600">üîÑ Last Updated: {member.updatedAt}</p>
            )}
          </div>
        </div>
      </div>

      {/* Meet the Team */}
      <div className="max-w-7xl mx-auto mt-28">
        <h3 className="text-4xl font-bold text-center text-gray-900 mb-14 tracking-tight drop-shadow-md">
          Meet the Team
        </h3>

        <div className="grid sm:grid-cols-2 md:grid-cols-3 gap-10 px-4">
          {teamMembers
            .filter((m) => m.id !== member.id)
            .slice(0, 3)
            .map((related) => (
              <Link
                key={related.id}
                href={`/about/${related.id}`}
                className="group relative rounded-3xl shadow-lg hover:shadow-2xl transition-all duration-500 overflow-hidden cursor-pointer perspective"
              >
                {/* Card Background */}
                <div className="relative w-full h-64 overflow-hidden rounded-t-3xl transform transition-transform duration-700 group-hover:rotate-y-2 group-hover:-translate-y-1 shadow-inner">
                  <Image
                    src={related.image}
                    alt={related.name}
                    fill
                    className="object-cover group-hover:scale-105 transition-transform duration-700 ease-out"
                  />
                  {/* Gradient Overlay */}
                  <div className="absolute inset-0 bg-gradient-to-t from-white/80 via-white/60 to-transparent rounded-t-3xl"></div>
                  {/* Floating Shine Effect */}
                  <div className="absolute inset-0 pointer-events-none bg-gradient-to-r from-transparent via-white/20 to-transparent opacity-0 group-hover:opacity-30 transition-opacity duration-700 rounded-t-3xl animate-shine"></div>
                </div>

                {/* Card Info */}
                <div className="p-6 text-center space-y-2 bg-white/90 backdrop-blur-lg rounded-b-3xl border-t border-gray-100 shadow-inner">
                  <h4 className="text-xl font-bold text-gray-900 group-hover:text-sky-700 transition-colors duration-300">
                    {related.name}
                  </h4>
                  <p className="text-gray-600 text-sm uppercase tracking-wide">
                    {related.title}
                  </p>
                </div>

                {/* Bottom Glow Accent */}
                <div className="absolute -bottom-6 left-1/2 -translate-x-1/2 w-24 h-24 rounded-full bg-sky-100/30 blur-3xl opacity-80 pointer-events-none"></div>
              </Link>
            ))}
        </div>
      </div>
    </main>
  );
}
